import './styles/profile.css';
import profile from '../img/profile.svg';

export default function Profile(){
	return (
		<div className="profile">
			<h2>Мой профиль</h2>
			<img src={profile} className="photo"/>
			<div className="info">
				<p>Отделение: ИТ</p>
				<p>Семестр: ИТ</p>
				<p>Срок обучения: 2021-2025</p>
			</div>
		</div>
	);
}
